import { DefaultGearOptimizer } from '@/services/gear-optimizer';
var ACTIONS = {
  OPTIMIZE: 'optimize',
  //
  PROGRESS: 'progress'
};

var reportProgress = function reportProgress(x) {
  self.postMessage({
    action: ACTIONS.PROGRESS,
    result: x
  });
};

addEventListener('message', function (event) {
  console.log('optimizer::message::start, event =', event);

  if (event.data.action == ACTIONS.OPTIMIZE) {
    // (self as any).postMessage({
    //   action: 'optimize-result',
    //   result: GearOptimizer.optimize(event.data.store, event.data.criteria, reportProgress)
    // });
    var optimizer = new DefaultGearOptimizer(event.data.store, event.data.profile, reportProgress);
    self.postMessage({
      action: 'optimize-result',
      result: optimizer.optimize()
    });
  } else {
    // hack of casting self to DedicatedWorkerGlobalScope, typescript conflict between webworker and other lib
    self.postMessage({
      action: 'START'
    });
    self.postMessage({
      action: 'END',
      result: [1, 2, 3]
    });
  }
});